import { DateTime } from 'luxon'

import api from '@/api'
import exportCsv from '@/common/mixins/export-csv'
import HourlyPeopleChart from './components/HourlyPeopleChart.vue'

export default {
  mixins: [exportCsv],
  components: { HourlyPeopleChart },

  data () {
    const now = DateTime.local().startOf('hour')

    return {
      maxDate: now.toFormat('yyyy-LL-dd'),
      date: now.toFormat('yyyy-LL-dd'),
      dateMenu: false,

      zones: [],
      zone: null,
      zoneQuery: null,
      isZonesLoading: false,
      error: null,

      isLoading: false,

      selectedZone: null,
      data: [],
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true
            }
          }],
          xAxes: [{
            display: true,
            labelString: 'Hour'
          }]
        }
      }
    }
  },

  watch: {
    zoneQuery (val) {
      this.zoneSearch(val)
    }
  },

  computed: {
    formDateDisplay () {
      return DateTime.fromFormat(this.date, 'yyyy-LL-dd').toFormat('dd LLL yyyy')
    },

    chartData () {
      const labels = []
      const data = []
      for (let i = 0; i <= 23; i++) {
        labels.push(i)
        if (this.data.length > 0) {
          data.push(this.data[i])
        } else {
          data.push(0)
        }
      }

      return {
        labels,
        datasets: [{
          label: '# of people',
          backgroundColor: '#f87979',
          data,
          borderWidth: 1
        }]
      }
    },
    plainData () {
      return this.data.map(r => Object.values(r))
    },
    exportCsvDisabled () {
      return this.isLoading || this.data.length === 0
    }
  },

  methods: {
    zoneSearch (zoneName) {
      // Items have already been requested
      if (this.isZonesLoading) return

      this.isZonesLoading = true
      const vm = this

      // Lazily load input items
      api.getZonesByName(zoneName)
        .then(results => {
          vm.isZonesLoading = false
          vm.zones = results
        })
        .catch(err => {
          vm.isZonesLoading = false
          vm.error = err.message || err
        })
    },

    getData () {
      if (this.isLoading) {
        return
      }

      const vm = this
      const selectedZone = vm.zone
      const date = vm.date

      vm.isLoading = true
      api.getPeopleCountHourlyInZone(selectedZone, date)
        .then(result => {
          vm.data = result.map(row => row.persons_count)
          vm.isLoading = false
        })
        .catch(err => {
          vm.isLoading = false
          vm.error = err.message || err
        })
    },

    exportToCsv () {
      const data = this.data.map((r, idx) => [idx < 10 ? `0${idx}` : `${idx}`, r])
      const headers = ['hour', 'persons_count']
      this.exportCsv(data, headers, `${this.date}_${this.zone}_zone-people-hourly-count.csv`)
    }
  }
}
